import SideMenu  from '@/blockComponents/sideMenu'// 사이드메뉴
import Toast     from '@/modal/toast'             // 토스트 모달
export default {
    name: "defaultLayout",
    components:{
      SideMenu,   // 사이드 메뉴,
      Toast      // 토스트 모달
    },
    data(){
      return{
        login       : true,   // 로그인 했을 시 on
        userName    : '곰돌이', // 로그인 시 유저 이름
        closeMenu   : false,  // 사이드 메뉴 of off
        searchActive: false,  // 검색 on off
        scTimer     :0,       // 검색 on off
        scY         :0,       //스크롤 높이
      }
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
    },
    methods:{
      // my fans 준비중 alert
      ready(){
        alert('지금은 준비 중입니다.')
      },
      // 사이드 메뉴 닫기
      closeBtn(){
        this.closeMenu    = false
        this.searchActive = false
      },
      // 검색 on off
      searchBtn(){
        if(this.searchActive == false )
          this.searchActive = true 
        else
          this.searchActive = false
      },
      // 스크롤 탑 이벤트
      handleScroll(){
        if (this.scTimer) return;
          this.scTimer   = setTimeout(() => {
            this.scY     = window.scrollY;
            clearTimeout(this.scTimer);
            this.scTimer = 0;
          }, 100);
      },
      // 스롤 탑 버튼
      toTop(){
        window.scrollTo({
          top: 0,
          behavior: "smooth"
        });
      }
    }
}
