    ],
  }),
  methods: {
    // my fans 준비중 alert
    ready(){
      alert('지금은 준비 중입니다.')
    },
    // 마이 페이지 열기
    myPage(){
      this.myMenuActive == true ?
      this.myMenuActive =  false:
      this.myMenuActive =  true;
    },
    // 마이 페이지 열기
    support(){
      this.supportActive == true ?
      this.supportActive =  false:
      this.supportActive =  true;
    },
    // 서포트 열기

    // 검색 on off
    searchBtn(){
      this.$emit('searchBtn')
    },
    // 메뉴 닫기 버튼
    closeBtn(){
      this.$emit('close')
    },
  }
}
